.button {
    list-style-type: none;
    font-size: 20px;
    padding: 10px;
    margin: 0px;
    text-decoration: none;
    color: #ffffff;
    justify-content: center;
}

.button:hover {
    transform: scale(1.1);
    transition-duration: 0.1s;
}

@media (max-width: 768px) {
    .button {
        padding: 0px
    }
    .button:hover {
        transform: scale(1.25);
        transition-duration: 0.1s;
    }    
}
