/* Main content styling */
.Main {
    background-color: #ffffff;
    padding: 20px;
    margin-left: 25%; /* Add margin to the left to account for the fixed navbar width */
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start; /* Align content to the top */
    align-items: flex-start; /* Align content to the left */
    overflow-y: auto; /* Ensure main content is scrollable if needed */
    color: #000; /* Ensure text color is visible */
  }

@media (max-width: 768px){
    .Main {
        width: 100%; /* Main content takes the full width */
        height: auto;
        margin-left: 0%; /* Remove left margin on smaller screens */
        border: none; /* Remove debug border */
    }
}
